import React, { Component } from 'react';
import './App.css';
import Button from 'react-bootstrap/Button';

class SecurityPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: <FloorOne/>,
      
    }
  }

  changePage(page){
    switch(page) {
      case "1":
        this.setState({
          currentPage: <FloorOne/> 
        });
        break;
      case "2":
        this.setState({
          currentPage: <FloorTwo/>}
          );
        break;
      case "3":
        this.setState({
          currentPage: <FloorThree/>}
          );
        break;
      case "4":
        this.setState({
          currentPage: <FloorFour/>}
          );
        break;
      case "5":
        this.setState({
          currentPage: <FloorFive/>}
          );
        break;
      default:
        break;
      }
    }
  render() {
    return(
      <div className="App-header">
        
          <p>
            <a href="#" onClick={() => this.changePage("1")}>Floors 1-10</a>
            __
            <a href="#" onClick={() => this.changePage("2")}>Floors 11-20</a>
            __
            <a href="#" onClick={() => this.changePage("3")}>Floors 21-30</a>
            __
            <a href="#" onClick={() => this.changePage("4")}>Floors 31-40</a>
            __
            <a href="#" onClick={() => this.changePage("5")}>Floors 41-50</a>
          </p>
          {this.state.currentPage}
      </div>
    )
  }
}


